.cards-table-container {
  margin: 20px;
}

.cards-table {
  width: 100%;
  border-collapse: collapse;
}

.cards-table th,
.cards-table td {
  border: 1px solid #ddd;
  padding: 10px;
  text-align: left;
  color: #ffcb05;
}

.cards-table tr:nth-child(even) {
  background-color: #3d7dca;
  opacity: 0.9;
}

.cards-table tr:nth-child(odd) {
  background-color: #003a70;
  opacity: 0.9;
}

.card-image {
  width: 100px;
  height: auto;
  border-radius: 5px;
}

.cards-table-autocomplete {
  width: 100%;
  padding: 20px;
}
.cards-table-autocomplete input {
  padding: 10px;
  border: 0;
  box-shadow: 0 0 15px 4px rgba(0, 0, 0, 0.06);
  border: 1px solid #ffcb05;
  width: 50%;
  background-color: #003a70;
  opacity: 0.9;
  color: #ffcb05;
}

input[type="checkbox"] {
  position: relative;
  width: 40px;
  height: 20px;
  appearance: none;
  background: #cccccc;
  outline: none;
  border-radius: 20px;
  box-shadow: inset 0 0 5px rgba(255, 0, 0, 0.2);
  transition: 0.7s;
}

input:checked[type="checkbox"] {
  background: #802323;
}

input[type="checkbox"]:before {
  content: "";
  position: absolute;
  width: 20px;
  height: 20px;
  border-radius: 20px;
  top: 0;
  left: 0;
  background: #ffcb05;
  transform: scale(1.1);
  transition: 0.2s;
}

input:checked[type="checkbox"]:before {
  left: 20px;
}
